.App {
	margin-top: 5%;
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
	/* background-color: #282c34; */
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
  }
  to {
		transform: rotate(360deg);
  }
}

h1 {
	color: black;
}

.selected-page {
	color: red;
	font-weight: bold;
}

.preloader-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.preloader-logo {
		animation: App-logo-spin infinite 0.7s linear;
  }
}

.table-head {
	cursor: pointer;
}

.add-new-data {
	position: absolute;
}